import { PiStarFourBold } from "react-icons/pi";
import roccoBanner from "../Images/img/roccobanner.png";
import rocco1 from "../Images/img/rocco1.png";
import rocco2 from "../Images/img/rocco2.png";
import rocco3 from "../Images/img/rocco3.png";
import rocco4 from "../Images/img/rocco4.png";
import rocco5 from "../Images/img/rocco5.png";
import rocco6 from "../Images/img/rocco6.png";
import rocco7 from "../Images/img/rocco7.png";
import rocco8 from "../Images/img/rocco8.png";
import rocco9 from "../Images/img/rocco9.png";
import rocco10 from "../Images/img/rocco10.png";
export default function Rocco() {
    return <div className="">
          <img className="projectBanner" src={roccoBanner} alt="banner-rocco"/>
      <div className="projectitle">
        <h1>Seasons of Rocco</h1>
        <h3>Assets voor Seasons of Rocco</h3>
      </div>
         <div className="skillsbanner">
                <span className="skillspan">
                    <PiStarFourBold />
                </span>
                <span className="skillspan">
                    Vaardigheden 
                </span>
                <span className="skillspan">
                    <PiStarFourBold />
                </span>
                <span className="skillspan">
                    Blender
                </span>
                <span className="skillspan">
                    Concept art
                </span>
            </div>
            <div className="projectcontent">
            Tijdens mijn opleiding, heb ik deelgenomen aan de games minor. Bij deze minor deeden meerdere leerlingen mee aan het maken van twee games gedurende een half jaar. Een voor mobiel (Pandemonium) en de ander voor een release op Steam.
            Binnen een team van 20 heb ik gewerkt als artist. Tijdens dit project heb ik concept art, 3D assets en textures gemaakt om gebruikt te worden binnen de game.
            De 3D modelen en texturen zijn gemaakt binnen het programma Blender.
            <div className="gallery">
              <div className="galleryColumn">
              <img className="projectImage" src={rocco1} alt="rocco"/>
              <img className="projectImage" src={rocco2} alt="rocco"/>
              <img className="projectImage" src={rocco3} alt="rocco"/>
              </div>
              <div className="galleryColumn">
              <img className="projectImage" src={rocco4} alt="rocco"/>
              <img className="projectImage" src={rocco5} alt="rocco"/>
              <img className="projectImage" src={rocco6} alt="rocco"/>
              </div>
              <div className="galleryColumn">
                <img className="projectImage" src={rocco7} alt="rocco"/>
                <img className="projectImage" src={rocco8} alt="rocco"/>
                </div>
              <div className="galleryColumn">
              <img className="projectImage" src={rocco9} alt="rocco"/>
              <img className="projectImage" src={rocco10} alt="rocco"/>
              </div>
            </div>
            <h3>Links</h3>
            <ul className="callList">
            <li>
              <h2 >
                <a className="callA" href="https://store.steampowered.com/app/2700510/Seasons_of_Rocco/">Steam Page</a></h2>
              </li>
              </ul>
            </div>  
    </div>;
  }
  